@import "variables";
@import "fonts";
@import "mixins";
.header{
  box-shadow: 0px 0px 10px rgba(0,0,0,.8);
}
.header {
  &_content {
    max-width: $maxWidthContainer;
    margin: 0 auto;
    height: 100px;
    padding: 0px 15px ;
    background-color: $primary-color ;
  }

  &_logo img {
    width: 80px;
    height: 80px;
    margin: 10px 0px 10px 0px;
  }

  &_menu {
    margin: -95px 0px 0px 0px;
    height: 80px;
    align-items: center;
    display: flex;
    justify-content: flex-end;
  }
  @media (max-width: $minWidth) {
    &._active{
      width: 100%;
      height: 330px;
    }
  }
}

.menu {
  &_icon {
  }

  &_body {
    @media (max-width: $minWidth) {
      transform: translate(0px , -190%);
      width: 100%;
      height: 100%;
    }
    &._active{
      transform: translate(0px , 180%);
      background-color: white;
    }
  }
  &_backraund{
    background-color: $secondary-color;
    width: 30px;
    height: 20px;
  }
  &_list {


    @media (min-width: $minWidth){
      display: flex;
      li {
        margin: 0px 0px 0px 40px;
      }
    }
    @media (max-width: $minWidth) {
      margin: -55px 0 0 -800px;

      text-align: center;
      background-color: $primary-color;
      li{
        margin: 15px 0px 0 0px;
      }
    }
  }

  &_link {
    font-size: 16px;
    color: $color-text  ;
    cursor: pointer ;
    text-decoration: none;
    transition: color 0.3s ease 0s;
  }
  a:hover{
    background-color: $secondary-color;
    color: $primary-color;
    transition: background-color .19s cubic-bezier(.2, .62, .38, 1);
    border-radius: 3.25rem;
    padding: 15px 20px;
    text-align: center;
  }
}

.icon-menu {
  display: none;
  @media (max-width: $minWidth) {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    margin-left: 800px;

    span {
      transition: all 0.3s ease 0s;
      top: calc(50% - 1px);
      left: 0px;
      position: absolute;
      width: 30px;
      height: 2px;
      background-color: $background-color;

      &:first-child {
        top: 0px;
      }

      &:last-child {
        top: auto;
        bottom: 0px;
      }
    }
    &._active {
      span {
        transition: all 0.3s ease 0s;
        left: 0px;
        position: absolute;
        width: 30px;
        height: 2px;
        background: $background-color;
        &:first-child {
          top: 0px;
        }

        &:last-child {
          top: auto;
          bottom: 0px;
        }
      }
    }
  }
}